* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  width: 80%;
  text-align: left;
  margin: 10px auto;
  position: relative;
}



li:nth-child(odd)::after {
  content: 'üéà';
  position: absolute;
  top: 50%;
  left: -30px;
  transform: translateY(-50%);
}

li:nth-child(even)::after {
  content: 'üòÄ';
  position: absolute;
  top: 50%;
  left: -30px;
  transform: translateY(-50%);
}
/* nth-child(2n) = 2Î∞∞Ïàò
nth-child(odd) = ÌôÄÏàò
nth-child(even) = ÏßùÏàò */


body {
  color: #333;
  line-height: 1;
  background-color: #a8c8f9;
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: #ffffff;
  text-align: center;
  padding: 30px 0;
}

input {
  width: 80%;
  padding: 30px;
  border: 3px solid #a8c8f9;
  border-radius: 100px;
  margin: 20px auto 40px auto;
  display: block;
  font-size: 18px;
}

input:focus {
  outline: none;
}

input::placeholder {
  font-size: 16px;
}

button {
  padding: 20px 70px;
  margin: 40px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border: 0;
  border-top: 2px solid #a8c8f9;
  border-bottom: 2px solid #a8c8f9;
  cursor: pointer;
}